import React from 'react'
import { motion } from "framer-motion";

const Profile = (props) => { 
  return (
    <motion.div
        initial={{opacity: 0}}
        animate={{opacity: 1}}
        exit={{opacity: 0}}
    >
      <h2 className="profile_title">WEB PUBLISHER<span></span></h2>
      <div className="profile_list">
          <div>
            <p className="tit">Skill</p>
            <ul>
              <li>HTML</li>
              <li>CSS</li>
              <li>SCSS</li>
              <li>jQuery</li>
              <li>Javascript</li>
              <li>React</li>
              <li>Git</li>
              <li>nexacro</li>
              <li>(Web Accessibility)</li>
              <li></li>
            </ul>
          </div>
          <div>
            <p className="tit">Tool</p>
            <ul>
              <li>VSCode</li>
              <li>Github</li>
              <li>GitLab</li>
              <li>Bitbucket</li>
              <li>Photoshop</li>
              <li>Adobe XD</li>
              <li>Figma</li>
              <li>Zeplin</li>
            </ul>  
          </div>
          <div>
            <p className="tit">Career</p>
            <dl>
              <dt>2022.07 ~ </dt>
              <dd> :    </dd>
              <dd> : SK  </dd>
            </dl>
            <dl>
              <dt>2019.07 ~ 2022.06 </dt>
              <dd> :    </dd>
              <dd> : LG (), IOT(), 
                <br /> SK   /   /  
              </dd>
            </dl>
          </div>
      </div>  
    </motion.div>
  )
}

export default Profile;
